body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* ## Colors
### Primary
- Desaturated Dark Cyan: hsl(180, 29%, 50%)
### Neutral
- Light Grayish Cyan (Background): hsl(180, 52%, 96%)
- Light Grayish Cyan (Filter Tablets): hsl(180, 31%, 95%)
- Dark Grayish Cyan: hsl(180, 8%, 52%)
- Very Dark Grayish Cyan: hsl(180, 14%, 20%)

### Body Copy
- Font size: 15px

### Headings
- Family: [Spartan](https://fonts.google.com/specimen/Spartan)
- Weights: 500, 700 */

@font-face {
  font-family: Spartan-Variable;
  src: url(/static-jobs-listings-new/static/media/Spartan-VariableFont_wght.db2678ac.ttf);
}

@font-face {
  font-family: Spartan-Bold;
  src: url(/static-jobs-listings-new/static/media/Spartan-Bold.776b89b4.ttf);
}

body {
  background-color: hsl(180, 52%, 96%);
  font-size: 15px;
  font-family: Spartan-Variable, sans-serif;
}

header {
  height: 120px;
  background-color: hsl(180, 29%, 50%);
  background-image: url(/static-jobs-listings-new/static/media/bg-header-desktop.6e2f7196.svg);
  margin-bottom: 70px;
}

main {
  margin: 0 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h3 {
  font-family: Spartan-Bold, sans-serif;
}

@media only screen and (max-width: 1240px) {
  body {
    font-size: 13px;
  }
  
  header {
    height: 155px;
    background-position-x: -3730px;
    /* background-position-y: 10px; */
    margin-bottom: 50px;
  }
  
  main {
    margin: 0 25px;

    display: flex;
    flex-direction: column;
  }
}
.card {
    border-radius: 5px;
    margin: 10px 0 20px 0;
    padding: 30px 40px;
    background-color: white;
    box-shadow: 5px 10px 15px hsl(180, 24%, 82%);
    color: hsl(180, 29%, 50%);
    width: 70vw;

    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
}

.strip {
    position: absolute;
    left: 0px;

    background-color: hsl(180, 29%, 50%);
    width: 5px;
    height: 100%;
    border-radius: 5px 0 0 5px;
}

.no-strip {
    position: absolute;
    left: 0px;

    background-color: white;
    width: 5px;
    height: 165px;
    border-radius: 5px 0 0 5px;
}

.main-info {
    display: flex;
    align-items: center;    
}

.info {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
}

.upper-info {
    display: flex;
    align-items: center;
}


.lower-info {
    display: flex;
    flex-direction: row;
}

.buttons {
    display: flex;
    justify-content: right;
}

@media only screen and (max-width: 1240px) {
    .card {
        margin: 10px 0 30px 0;
        padding: 10px 0px;
        width: 85vw;
    
        position: relative;
        top: -20px;

        flex-direction: column;
        align-items: flex-start;
    }
    
    .main-info {
        flex-direction: column;
        padding-left: 5px;
    }

    .main-logo {
        width: 100%;
        height: 20px;
        display: flex;
        flex-direction: row;
        justify-content: left;
        margin-left: 40px;
    }
    
    .info {
        display: flex;
        flex-direction: column;
        margin-left: 20px;
    }
    
    .upper-info {
        font-family: Spartan-Bold, sans-serif;
    }
    
    .buttons {
        margin: 15px 25px 5px;
        padding-top: 10px;

        border-top: 1px solid hsl(180, 8%, 52%);
        display: flex;
        justify-content: left;
        flex-wrap: wrap;
    }

    .strip {
        top: 0px;
    }


}

img {
    height: 100px;
}

@media only screen and (max-width: 1240px) {
    img {
        height: 50px;
    }

    .logo {
        position: relative;
        top: -35px;
    }
}
.company {
    font-weight: 700;
    margin: 0;
}

.new-and-featured {
    display: flex;
    color: white;
    font-weight: bold;
    font-size: 10px;
    align-items: center;

}

.new {
    background-color: hsl(180, 29%, 50%);
    padding: 5px 5px 2px 5px;
    margin: 5px 5px 5px 10px;
    border-radius: 10em;

}

.featured {
    background-color: hsl(180, 14%, 20%);
    padding: 5px 5px 2px 5px;
    margin: 5px 5px 5px 0px;
    border-radius: 10px;
}

.false {
    display: none;
}
h3 {
    margin: 10px 0 8px 0;
    color: hsl(180, 14%, 20%);
}

h3:hover {
    color: hsl(180, 29%, 50%);
    cursor: pointer;
}
.lowerInfo {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.PLowerInfo {
    color: hsl(180, 8%, 52%);
    font-size: 16px;
    font-weight: 700;
    margin: 0;
}

.mid-point {
    height: 3px;
    width: 3px;
    border-radius: 1.5px;
    background-color: hsl(180, 8%, 52%);
    margin: 15px;
}

@media only screen and (max-width: 1240px) {
    .PLowerInfo {
        font-size: 13px;
    }
}
.buttonsList {
    display: flex;
    list-style-type: none;
    font-family: Spartan-Bold, sans-serif;
    font-size: 14px;
    
    margin: 5px;
    padding: 8px 7px 5px 7px;
    border-radius: 5px;
    background-color: hsl(180, 52%, 96%);
}

.buttonsList:hover {
    background-color: hsl(180, 29%, 50%);
    color: hsl(180, 52%, 96%);
    cursor: pointer;
}

@media only screen and (max-width: 1240px) {
    .buttonsList {
        margin: 5px 15px 10px 0;
        
    }
}
.filter-bar-main {
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 90px;
    width: 70vw;
    height: 70px;
    box-sizing: border-box;

    background-color: white;
    padding: 20px 20px 20px 30px;
    border-radius: 5px;
    box-shadow: 5px 10px 15px hsl(180, 24%, 82%);

    font-family: Spartan-Bold, sans-serif;
}

.hide-filter-bar {
    display: none;
}

.left {
    display: flex;

}

.right {
    color: hsl(180, 8%, 52%);
    font-size: 14px;
    padding: 0 20px;
    display: flex;
    align-items: center;
}

.right:hover {
    text-decoration: underline;
    cursor: pointer;
    color: hsl(180, 29%, 50%);
}

@media only screen and (max-width: 1240px) {
    .filter-bar-main {
        position: relative;
        top: -90px;
        width: 85vw;
        height: auto;
        padding: 8px 7px;

    }    
    .left {
        display: flex;
        flex-wrap: wrap;
    }
}
.whole-button {
    display: flex;
    align-items: center;
    color: hsl(180, 29%, 50%);

    margin: 0 10px;
}

.main-button {
    background-color: hsl(180, 52%, 96%);
    padding: 8px 5px 4px 5px;
    border-radius: 5px 0 0 5px;
}

.close-button {
    display: flex;
    align-items: center;
    background-color: hsl(180, 29%, 50%);
    padding: 7.5px 8px 7.5px 8px;
    border-radius: 0 5px 5px 0;
}

.x-button {
    width: 15px;
    height: 15px;
}

.close-button:hover {
    background-color: hsl(180, 14%, 20%);
    cursor: pointer;
}

@media only screen and (max-width: 1240px) {
    .whole-button {
        margin: 12px 0px 12px 10px;
    }
    
    .main-button {
        background-color: hsl(180, 52%, 96%);
        padding: 8px 5px 4px 5px;
        border-radius: 5px 0 0 5px;
    }

}
